/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.container {
  position: relative;
  display: flex;
}

.command-value {
  display: flex;
  flex-grow: 1;
  align-items: center;
  font-family: ui-monospace, SFMono-Regular, SF Mono, Menlo, Consolas, Liberation Mono, monospace;
  font-size: 13px;
}
.command-value--no-visible {
  opacity: 0;
}

.copy-message {
  position: absolute;
  left: 0;
  padding-top: 4px;
}
.copy-message--no-visible {
  display: none;
}

.toolbox {
  display: block;
  width: 26px;
  height: 34px;
  margin: -0.1em 0 0 auto;
  padding: 0;
  list-style: none;
}

.copy-button {
  width: auto;
  height: auto;
  margin-top: 0.3em;
  margin-right: -0.6em;
  opacity: 0.55;
}
.copy-button .pipeline-icon {
  width: 26px;
  height: 26px;
}
.copy-button:hover {
  opacity: 1;
}
.copy-button:active {
  transform: translateY(1px);
  opacity: 0.85;
}
