/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.pipeline-wrapper {
  height: 100%;
}

.pipeline-wrapper__loading {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translateX(136px);
  transition: transform 0.4s ease, width ease 0.4s;
  width: calc(100% - 136px);
}
@media (min-width: 700px) {
  .pipeline-wrapper__loading--sidebar-visible {
    transform: translateX(480px);
    width: calc(100% - 480px);
  }
}
