/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
/** Implementation **/
.search-input-wrapper {
  width: 100%;
  max-width: 320px;
}

.search-input {
  /** padding top can be overriden if label is generated (see below) **/
  padding: 7px 11px;
  border: 1px solid #2a3b46;
  box-sizing: border-box;
}
.search-input--disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.search-input--focused.search-theme--light {
  outline: 4px solid;
}
.search-input--focused.search-theme--dark {
  outline: 4px solid;
}
[data-whatintent=keyboard] .search-input--focused {
  box-shadow: none;
}
[data-whatintent=mouse] .search-input--focused {
  outline: none;
}

.search-input__field {
  background: transparent;
  border: none;
  font-family: "Titillium Web", sans-serif;
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 0.1px;
  line-height: 1.3;
  outline: none;
  padding: 0;
  width: 100%;
  /** more specific selector needs to be stated so the changes apply **/
}
.search-input__field, .search-input__field:hover, .search-input__field:active, .search-input__field:focus {
  box-shadow: none;
}
.search-input.search-input--disabled .search-input__field {
  cursor: not-allowed;
}

.search-theme--dark .search-input__field {
  color: #d9dcde;
}

.search-input__label {
  display: flex;
  font-size: 1.4em;
  letter-spacing: 0.1px;
  line-height: 1.4;
  margin-bottom: 4px;
}

.search-input__line {
  display: flex;
  font-size: 1.6em;
  font-weight: 400;
  height: 1px;
  letter-spacing: 0.1px;
  line-height: 1.3;
  margin-top: 3px;
  pointer-events: none;
}
.search-input__line::before {
  content: attr(data-value);
  overflow: hidden;
  white-space: pre;
  background-color: transparent;
  color: transparent;
  transition: background-color 0.3s;
}
.search-input__line::after {
  content: "";
  flex: 0;
  background-color: transparent;
  transition: background-color 0.3s, flex 0.3s;
}
.search-input__field:focus + .search-input__line::after, .search-input__field:active + .search-input__line::after {
  flex: 1;
  transition-timing-function: ease-out;
}
