/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.kui-theme--light {
  --color-default-alt: #000000;
  --color-text: #151515;
  --color-text-alt: #d9dcde;
  --color-text-faded: #242424;
  --color-bg-1: #ccd0d3;
  --color-bg-2: #e6e7e9;
  --color-bg-3: #f2f3f4;
  --color-bg-4: #ffffff;
  --color-bg-5: #d9dcde;
  --color-bg-alt: #242424;
  --color-sidebar-background: #f2f3f4;
  --color-global-toolbar-background: #f9f9fa;
  --color-sidebar-border: #ccd0d3;
  --color-sidebar-shadow: #c0c5c9;
  --color-run-list-divider: #d9dcde;
  --color-run-list-hover: #e6e7e9;
  --color-base-20: #c0c5c9;
  --color-black-10: #f2f3f4;
}

.kui-theme--dark {
  --color-default-alt: #ffffff;
  --color-text: #d9dcde;
  --color-text-alt: #151515;
  --color-text-faded: #989898;
  --color-bg-1: #001521;
  --color-bg-2: #091d29;
  --color-bg-3: #091d29;
  --color-bg-4: #132631;
  --color-bg-5: #2a3b46;
  --color-bg-alt: #ffffff;
  --color-sidebar-background: #091d29;
  --color-global-toolbar-background: #001521;
  --color-sidebar-border: #c0c5c9;
  --color-sidebar-shadow: #021218;
  --color-run-list-divider: #2a3b46;
  --color-run-list-hover: #1c2e3a;
  --color-base-20: #5e6c74;
  --color-black-10: #091d29;
}

.kedro {
  -webkit-font-smoothing: antialiased;
  font-family: "Titillium Web", sans-serif;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.1px;
  line-height: 1.4;
}

*,
:after,
:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
