/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.search-bar {
  position: relative;
  width: 100%;
  max-width: 320px;
}

.search-bar .search-input {
  padding-left: 44px;
}

.icon {
  transition: 0.3s opacity;
}
.search-bar .icon {
  position: absolute;
  top: 50%;
  z-index: 1;
  opacity: 0.3;
  transform: translate(0, -50%);
}
.search-bar--focused .icon {
  opacity: 1;
}

.search-bar__icon-wrapper {
  transition: 0.3s opacity;
}
.search-bar__icon-wrapper .icon {
  top: calc(10px * 2);
  left: calc(24px / 2);
  width: 24px;
  height: 24px;
  opacity: 0.45;
  fill: var(--color-default-alt) !important;
}

.search-bar .search-input__field {
  max-width: calc(100% - 24px);
}

.search-bar .search-input__line::before {
  max-width: calc(100% - 24px);
}

.search-bar__dynamic-icon {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.1s, visibility 0.1s;
}
.search-bar__dynamic-icon--visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.1s;
}

.icon--close {
  background: none;
  border-radius: 0;
  border: none;
  box-shadow: none;
  cursor: pointer;
  height: 20px;
  outline: 4px solid transparent;
  padding: 0;
  position: absolute;
  right: 2px;
  top: 18px;
  transform: translateY(-50%);
  width: 20px;
}
.icon--close:focus {
  outline-color: #00bcff;
}
.search-bar .icon--close {
  right: 10px;
  width: 24px;
  height: 24px;
}

.icon__graphics {
  height: 100%;
  pointer-events: none;
  right: 0;
  width: 100%;
}

.search-bar__label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
