.pipeline-nodelist__row {
  position: relative;
  display: flex;
  align-items: center;
  height: 32px;
  transform: translate(0, 0);
  cursor: default;
}
.kui-theme--dark .Mui-selected .pipeline-nodelist__row--overwrite {
  background-color: #091d29;
}
.kui-theme--light .Mui-selected .pipeline-nodelist__row--overwrite {
  background-color: #f2f3f4;
}
.Mui-selected .pipeline-nodelist__row--overwrite:hover {
  background-color: var(--color-nodelist-row-active);
}
.pipeline-nodelist__row--kind-filter {
  padding: 0 1.6em 0 2.6em;
}
.pipeline-nodelist__row--active, .pipeline-nodelist__row--visible:hover {
  background-color: var(--color-nodelist-row-active);
}
.pipeline-nodelist__row--selected, .pipeline-nodelist__row--visible.pipeline-nodelist__row--selected {
  background-color: var(--color-nodelist-row-selected);
}
.pipeline-nodelist__row--disabled {
  pointer-events: none;
}

.pipeline-nodelist__row__icon {
  display: block;
  flex-shrink: 0;
  width: 2.2em;
  height: 2.2em;
  fill: var(--color-text);
}
.pipeline-nodelist__row__icon.pipeline-row__toggle-icon--focus-checked {
  fill: #00bcff;
}
.pipeline-nodelist__row__icon--disabled > * {
  opacity: 0.1;
}

.pipeline-nodelist__row__type-icon--nested > * {
  opacity: 0.3;
}
.pipeline-nodelist__row__type-icon--faded > * {
  opacity: 0.2;
}
.pipeline-nodelist__row__type-icon--active > *, .pipeline-nodelist__row__type-icon--selected > *, .pipeline-nodelist__row--visible:hover .pipeline-nodelist__row__type-icon > *, [data-whatintent=keyboard] .pipeline-nodelist__row__text:focus .pipeline-nodelist__row__type-icon > * {
  opacity: 1;
}
.pipeline-nodelist__row__type-icon--active--faded > *, .pipeline-nodelist__row__type-icon--selected--faded > *, .pipeline-nodelist__row--visible:hover .pipeline-nodelist__row__type-icon--faded > *, [data-whatintent=keyboard] .pipeline-nodelist__row__text:focus .pipeline-nodelist__row__type-icon--faded > * {
  opacity: 0.55;
}

