.pipeline-nodelist__filter-panel {
  z-index: 1;
  background: var(--color-nodelist-filter-panel);
}
.pipeline-nodelist__filter-panel .pipeline-nodelist-section:last-child {
  padding-bottom: 28px;
  padding-top: 4px;
}

.pipeline-nodelist__elements-panel .pipeline-nodelist-section:last-child {
  padding-bottom: 28px;
  padding-top: 12px;
}

.pipeline-nodelist__split {
  position: absolute;
  top: 58px;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}
.pipeline-nodelist__split--resizing {
  cursor: row-resize;
}

.pipeline-nodelist__split-handle {
  position: relative;
  flex-grow: 0;
  flex-shrink: 0;
  height: 16px;
  border-top: 1px solid transparent;
  cursor: row-resize;
  transition: border-color ease 0.1s 0.15s;
}
.pipeline-nodelist__split-handle:after {
  position: absolute;
  top: 7px;
  left: 50%;
  display: block;
  width: 50px;
  height: 2px;
  background: var(--color-nodelist-split-handle);
  border-radius: 2px;
  transform: translate(-50%, 0);
  opacity: 0;
  transition: opacity ease 0.1s 0.15s;
  content: " ";
  pointer-events: none;
}
.pipeline-nodelist__filter-panel:hover .pipeline-nodelist__split-handle:after {
  opacity: 0.2;
}
.pipeline-nodelist__split-handle:focus {
  z-index: 1;
  outline: 3px solid #00bcff;
}
.pipeline-nodelist__split-handle:hover {
  border-top: 1px solid var(--color-nodelist-split-handle-border);
}
.pipeline-nodelist__filter-panel:hover .pipeline-nodelist__split-handle:hover:after {
  opacity: 0.3;
}
.pipeline-nodelist__split--resizing .pipeline-nodelist__split-handle {
  border-top: 1px solid #00bcff;
}

.pipeline-nodelist-scrollbars {
  flex-grow: 1;
}
.pipeline-nodelist-scrollbars:after {
  position: absolute;
  top: 0;
  right: -1px;
  left: -1px;
  height: 22px;
  background: linear-gradient(0deg, var(--color-nodelist-bg-transparent) 0%, var(--color-bg-3) 100%);
  content: "";
  pointer-events: none;
}
.pipeline-nodelist__filter-panel .pipeline-nodelist-scrollbars:after {
  display: none;
}

.kui-theme--light {
  --color-nodelist-row-active: #eaebed;
  --color-nodelist-row-selected: #dfe2e4;
  --color-nodelist-filter-indicator-on: #00bcff;
  --color-nodelist-filter-indicator-off: #242424;
  --color-nodelist-highlight: #00bcff;
  --color-nodelist-split-handle: #242424;
  --color-nodelist-split-handle-border: #eaebed;
  --color-nodelist-filter-panel: #ffffff;
  --color-nodelist-element-panel: #f2f3f4;
}

.kui-theme--dark {
  --color-nodelist-row-active: #042331;
  --color-nodelist-row-selected: #063b51;
  --color-nodelist-filter-indicator-on: #00bcff;
  --color-nodelist-filter-indicator-off: #848e95;
  --color-nodelist-highlight: #00bcff;
  --color-nodelist-split-handle: #ffffff;
  --color-nodelist-split-handle-border: #4c5b64;
  --color-nodelist-filter-panel: #132631;
  --color-nodelist-element-panel: #091d29;
}

.kui-theme--light {
  --color-nodelist-bg-transparent: rgba(242, 243, 244, 0);
}

.kui-theme--dark {
  --color-nodelist-bg-transparent: rgba(9, 29, 41, 0);
}

.kui-theme--light {
  --color-nodelist-bg-filter-transparent: rgba(255, 255, 255, 0);
}

.kui-theme--dark {
  --color-nodelist-bg-filter-transparent: rgba(19, 38, 49, 0);
}

.pipeline-nodelist {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-top: 1.8em;
}
.pipeline-nodelist:before,
.pipeline-nodelist > * {
  transition: opacity ease 0.2s;
}
.pipeline-nodelist:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: var(--color-bg-2);
  opacity: 0;
  content: "";
}
.pipeline-nodelist--fade > * {
  opacity: 0.2;
}
.pipeline-nodelist--fade:before {
  opacity: 1;
}

.kui-theme--dark .MuiTreeItem-label, .kui-theme--light .MuiTreeItem-label {
  padding-left: 0;
}
