.pipeline-toolbar__label {
  color: var(--color-text-alt);
  background: var(--color-bg-alt);
  box-shadow: 0 1px 3px #132631;
}

.pipeline-icon--container {
  position: relative;
}

.pipeline-icon-toolbar__button {
  background: none;
  border: none;
  box-shadow: none;
  color: inherit;
  cursor: pointer;
  font-size: 1.1em;
  height: 56px;
  padding: 0;
  position: relative;
  text-align: center;
  user-select: none;
  vertical-align: middle;
  width: 56px;
}
@media (max-height: 550px) {
  .pipeline-icon-toolbar__button {
    height: 42px;
  }
}
.pipeline-icon-toolbar__button:focus {
  outline: none;
}
[data-whatinput=keyboard] .pipeline-icon-toolbar__button:focus {
  outline: 3px solid #00bcff;
}
.pipeline-icon-toolbar__button:hover svg {
  opacity: 1;
}
.pipeline-icon-toolbar__button:disabled {
  cursor: not-allowed;
}
.pipeline-icon-toolbar__button:disabled svg {
  opacity: 0.2;
}
.pipeline-icon-toolbar__button svg {
  width: 2.175em;
  height: 2.175em;
}
.pipeline-icon-toolbar__button--active svg {
  opacity: 1;
}

.pipeline-toolbar__label {
  display: inline-block;
  font-size: 1.4em;
  opacity: 0;
  padding: 0.5em 0.7em;
  pointer-events: none;
  position: absolute;
  transition: opacity ease 0.1s;
  white-space: nowrap;
  z-index: 1;
}
@media (max-width: 700px) {
  .pipeline-sidebar--visible .pipeline-toolbar__label {
    left: auto;
    margin-left: auto;
    margin-right: 1.6em;
    right: 50%;
  }
}
.pipeline-icon-toolbar__button:hover .pipeline-toolbar__label, [data-whatinput=keyboard] .pipeline-icon-toolbar__button:focus .pipeline-toolbar__label {
  opacity: 1;
  pointer-events: all;
  transition-delay: 1s;
}
.pipeline-toolbar__label::after {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  content: "";
}
@media (max-width: 700px) {
  .pipeline-sidebar--visible .pipeline-toolbar__label::after {
    border-color: transparent transparent transparent var(--color-bg-alt);
    border-width: 7px 0 7px 7px;
    left: auto;
    right: -6.5px;
  }
}

.pipeline-toolbar__label-right {
  top: 50%;
  left: 50%;
  margin-top: -1.2em;
  margin-left: 1.6em;
}
.pipeline-toolbar__label-right::after {
  top: calc(50% - 7px);
  left: -6.5px;
  border-color: transparent var(--color-bg-alt) transparent transparent;
  border-width: 7px 7px 7px 0;
}

.pipeline-toolbar__label-left {
  top: 50%;
  right: 50%;
  margin-top: -1.2em;
  margin-right: 1.6em;
}
.pipeline-toolbar__label-left::after {
  top: calc(50% - 7px);
  right: 7.5px;
  border-color: transparent transparent transparent var(--color-bg-alt);
  border-width: 7px 0 7px 7px;
}

.pipeline-toolbar__label-bottom {
  left: 50%;
  transform: translateX(-50%);
  margin-top: 0.5em;
}
.pipeline-toolbar__label-bottom::after {
  bottom: 100%;
  left: calc(50% - 7px);
  border-color: transparent transparent var(--color-bg-alt) transparent;
  border-width: 0 7px 7px 7px;
}

.pipeline-toolbar__label-top {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 0.5em;
}
.pipeline-toolbar__label-top::after {
  top: 100%;
  right: calc(50% - 7px);
  border-color: var(--color-bg-alt) transparent transparent transparent;
  border-width: 7px 7px 0 7px;
}

.pipeline-icon {
  fill: var(--color-default-alt);
  opacity: 0.7;
  transition: opacity 300ms ease;
}
