/** Imports **/ /*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
/** Variables **/
.kui-theme--light {
  --color-button--border: #000000;
  --color-button--border--disabled: #abb2b7;
  --color-button__text--hover: #d9dcde;
  --color-button__text: #151515;
  --color-secondary-active: #f9f9fa;
}

.kui-theme--dark {
  --color-button--border: #ffffff;
  --color-button--border--disabled: #4c5b64;
  --color-button__text--hover: #151515;
  --color-button__text: #d9dcde;
  --color-secondary-active: #2a3b46;
}

.button {
  display: inline-block;
}

.button__btn {
  background: none;
  border-radius: 0;
  border: none;
  box-shadow: none;
  color: var(--color-button__text);
  cursor: pointer;
  display: flex;
  font-family: inherit;
  font-size: 1.6em;
  font-weight: 600;
  justify-content: center;
  letter-spacing: 0.1px;
  line-height: 1.3;
  margin: 2px;
  padding: 0.7em 0.8em 0.8em;
  transition: background ease 0.2s, border-color ease 0.2s, color ease 0.2s;
}

.button__btn--small {
  font-size: 1.4em;
  letter-spacing: 0.1px;
  line-height: 1.4;
  padding: 0.6em 0.85em 0.7em;
}

.button__btn--primary {
  border: 2px solid var(--color-button--border);
}
.button__btn--primary:active:not(:disabled) {
  background: var(--color-button--border);
  color: var(--color-button__text--hover);
  transition: none;
}
.button__btn--primary:focus {
  box-shadow: 0 0 0 7px #00bcff;
}
.button__btn--primary:hover:not(:disabled) {
  background: var(--color-button--border);
  color: var(--color-button__text--hover);
}
.button__btn--primary:disabled {
  border: 2px solid var(--color-button--border--disabled);
  color: var(--color-button--border--disabled);
  cursor: not-allowed;
}
.button__btn--primary:focus-visible {
  outline: none;
}
[data-whatinput=mouse] .button__btn--primary:focus {
  box-shadow: none;
}

.button__btn--secondary {
  outline: 4px solid transparent;
}
.button__btn--secondary::after {
  display: block;
  content: "";
  width: 100%;
  height: 2px;
  /* Offset the underline height so it doesn't affect text vertical alignment */
  margin-bottom: -2px;
  transform: translateY(8px);
  transition: transform ease 0.1s, background ease 0.1s;
  background: none;
}
.button__btn--secondary:focus {
  outline: 4px solid #00bcff;
}
.button__btn--secondary:hover::after {
  background: var(--color-button--border);
  transform: translateY(4px);
}
.button__btn--secondary:active {
  background-color: var(--color-secondary-active);
  outline-color: var(--color-secondary-active);
}
[data-whatinput=mouse] .button__btn--secondary:focus {
  outline: none;
}

.button__btn--success {
  animation: scale 0.35s ease;
  background-color: #31e27b;
  border: 2px solid #31e27b;
  color: #000000;
  width: 172px;
}
.button__btn--success:focus-visible {
  outline: none;
}

.success-check-mark {
  animation: grow 0.35s 0.4s ease forwards;
  display: block;
  margin-left: 0.5em;
  transform: scale(0);
}

@keyframes scale {
  0% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(1.15);
  }
  75% {
    transform: scaleX(0.9);
  }
  100% {
    transform: scaleX(1);
  }
}
@keyframes grow {
  0% {
    transform: scale(0);
  }
  80% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
