.pipeline-plot-modal__collapse-plot, .pipeline-plot-modal__back {
  padding: 0;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  background: none;
  border: none;
  box-shadow: none;
  cursor: pointer;
}
.pipeline-plot-modal__collapse-plot:focus, .pipeline-plot-modal__back:focus {
  outline: none;
}
[data-whatintent=keyboard] .pipeline-plot-modal__collapse-plot:focus, [data-whatintent=keyboard] .pipeline-plot-modal__back:focus {
  box-shadow: 0 0 0 3px #00bcff inset;
}

.kui-theme--light {
  --color-bg-plot: #e6e7e9;
  --color-button-plot: #005c7d;
  --color-button-plot-hovered: #0084b2;
  --color-button-text: #ffffff;
}

.kui-theme--dark {
  --color-bg-plot: #091d29;
  --color-button-plot: #005c7d;
  --color-button-plot-hovered: #0084b2;
  --color-button-text: #ffffff;
}

.pipeline-metadata-modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 80px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  background-color: var(--color-bg-plot);
}
.pipeline-metadata-modal .pipeline-plotly-chart,
.pipeline-metadata-modal .pipeline-matplotlib-chart {
  width: calc(98% - 80px - 80px);
  height: calc(92% - 80px);
  margin: auto;
}

.pipeline-plot-modal__top {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  min-width: 100%;
  padding: 1.5em;
}

.pipeline-plot-modal__header {
  display: flex;
  margin: 0 auto;
  padding: 0 32px;
}

.pipeline-plot-modal__icon {
  display: inline-block;
  width: 2.9em;
  height: 2.8em;
  margin: 0 10px 0 0;
  fill: var(--color-text);
}

.pipeline-plot-modal__title {
  margin-top: 1px;
  font-size: 1.8em;
}

.pipeline-plot-modal__back {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px 12px;
}

.pipeline-plot-modal__back-text {
  font-size: 1.6em;
}

.pipeline-plot-modal__back-icon {
  width: 1.9em;
  height: 1.9em;
  margin: 0 12px 0 0;
  fill: var(--color-text);
}

.pipeline-plot-modal__bottom {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-grow: 0;
  flex-shrink: 0;
  align-content: center;
  align-items: center;
  justify-content: flex-end;
  min-width: 100%;
}

.pipeline-plot-modal__collapse-plot {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: flex-end;
  width: 400px;
  background-color: var(--color-button-plot);
}
.pipeline-plot-modal__collapse-plot:hover {
  background-color: var(--color-button-plot-hovered);
}

.pipeline-plot-modal__collapse-plot-icon {
  width: 1.9em;
  height: 1.9em;
  margin: 0 6px -0.1em 36px;
  fill: var(--color-button-text);
}

.pipeline-plot-modal__collapse-plot-text {
  margin: 12px 10px 12px 5px;
  color: var(--color-button-text);
  font-size: 1.6em;
}

@media (max-width: 700px) {
  .pipeline-plot-modal__collapse-plot {
    width: auto;
  }
  .pipeline-plot-modal__collapse-plot-text {
    display: none;
  }
  .pipeline-plot-modal__collapse-plot-icon {
    margin: 10px;
  }
}
