@charset "UTF-8";
/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.accordion__heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.accordion__heading--hide {
  visibility: hidden;
}

.accordion__title {
  font-size: 1.2em;
  font-weight: 600;
}
.accordion__title--medium {
  font-size: 1.3em;
}
.accordion__title--large {
  font-size: 1.6em;
}

.accordion__title__detail {
  font-weight: normal;
  margin-left: 8px;
}

.accordion__toggle {
  background: none;
  border-radius: 50%;
  border: none;
  box-shadow: none;
  color: var(--color-default-alt);
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  height: 2.8em;
  line-height: 1em;
  padding: 0;
  text-align: center;
  transition: transform ease 0.1s;
  width: 1.15em;
}
.accordion__toggle:focus {
  outline: none;
}
[data-whatintent=keyboard] .accordion__toggle:focus {
  box-shadow: 0 0 0 3px #00bcff inset;
}
.accordion__toggle:before {
  content: "▾";
  font-size: 1.8em;
  opacity: 0.55;
}
.accordion__toggle:hover:before {
  opacity: 1;
}
.accordion__toggle--alt {
  transform: rotate(90deg);
}

.accordion__content--hide {
  display: none;
}

.accordion--left .accordion__heading {
  justify-content: flex-start;
}

.accordion--left .accordion__title {
  margin-left: 0.7em;
}

.accordion--left .accordion__toggle--alt {
  transform: rotate(-90deg);
}
