/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
/** Variables */
/* the height of each item/row */
.kui-theme--light {
  --menu-option-bg-hover: #d9dcde;
  --menu-option-divider: #ccd0d3;
  --menu-option-text-color: #151515;
}

.kui-theme--dark {
  --menu-option-bg-hover: #132631;
  --menu-option-divider: #21333e;
  --menu-option-text-color: #d9dcde;
}

.menu-option {
  align-items: center;
  border-bottom: 1px solid var(--menu-option-divider);
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  height: 40px;
  outline: none;
  overflow: hidden;
  padding: 0 1.8em 0 calc(1.8em - 2px);
  user-select: none;
  white-space: nowrap;
}
.menu-option:hover {
  background: var(--menu-option-bg-hover);
}
.menu-option:last-of-type {
  border-bottom: none;
}

.menu-option__content {
  align-items: center;
  color: var(--menu-option-text-color);
  display: flex;
  font-size: 1.6em;
  letter-spacing: 0.1px;
  line-height: 1.25;
  position: relative;
  text-overflow: ellipsis;
  width: 100%;
}
