in the Style Guide docs under 'Theme Colours' 
*/
.kui-theme--light {
  --pipeline-list-option-bg-active: #e6e7e9;
  --pipeline-list-option-text-color: #151515;
}

.kui-theme--dark {
  --pipeline-list-option-bg-active: #1c2e3a;
  --pipeline-list-option-text-color: #d9dcde;
}

.pipeline-list .dropdown__label,
.pipeline-list .menu-option {
  height: 58px;
}
.pipeline-list .dropdown__label.pipeline-list__option--active,
.pipeline-list .menu-option.pipeline-list__option--active {
  background: var(--pipeline-list-option-bg-active);
  border-left: 2px solid #00bcff;
  color: var(--pipeline-list-option-text-color);
}
