/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
/** Variables **/
.kui-theme--light {
  --color-modal-bg: #242424;
  --color-modal-content: #f9f9fa;
  --color-modal-title: #151515;
  --color-modal-description: #242424;
}

.kui-theme--dark {
  --color-modal-bg: #001521;
  --color-modal-content: #1c2e3a;
  --color-modal-title: #d9dcde;
  --color-modal-description: #989898;
}

/** Implementation **/
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  visibility: hidden;
  transition: visibility ease 0.4s;
}

.modal--visible {
  visibility: visible;
}

.modal__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--color-modal-bg);
  opacity: 0;
  transition: opacity ease 0.4s;
}

.modal__bg--visible {
  opacity: 1;
}

.modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  background: var(--color-modal-content);
  max-width: 400px;
  width: 100%;
  opacity: 0;
  transform: translate(-50%, -50%) translateY(80px);
  transition: opacity 0.4s, transform 0.4s;
}

.modal__content--visible {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.modal__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 40px;
  text-align: center;
}

.modal__title {
  font-size: 2em;
  line-height: 1.4;
  width: 100%;
  color: var(--color-modal-title);
  margin-bottom: 40px;
}

.modal__description {
  margin-bottom: 40px;
  color: var(--color-modal-description);
}

.modal .pipeline-icon {
  position: absolute;
  top: 10px;
  right: 10px;
}
