.pipeline-global-control-toolbar, .pipeline-global-routes-toolbar {
  display: block;
  list-style: none;
  margin: 0;
  padding: 1em 0;
}

.pipeline-global-toolbar {
  background: var(--color-global-toolbar-background);
  display: flex;
  flex-direction: column;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 80px;
  z-index: 5;
}

.pipeline-global-control-toolbar {
  margin-top: auto;
}

.pipeline-menu-button--large {
  height: 80px;
  width: 80px;
}

.pipeline-menu-button--link:hover {
  background-color: var(--color-bg-2);
}

.pipeline-menu-button--logo {
  margin-bottom: 40px;
}
.pipeline-menu-button--logo svg {
  height: 2.9em;
  opacity: 1;
  width: 2.9em;
}

.pipeline-global-routes-toolbar a.active .pipeline-menu-button--link {
  background-color: var(--color-bg-2);
}

.pipeline-global-routes-toolbar a.active .pipeline-menu-button--link svg {
  opacity: 1;
}

.update-reminder-dot {
  background-color: #ffbc00;
  border-radius: 50%;
  height: 8px;
  position: absolute;
  right: 28px;
  top: 31px;
  width: 8px;
}
