.pipeline-sidebar {
  background: var(--color-sidebar-background);
  border-bottom: 1px solid var(--color-sidebar-border);
  box-shadow: 0 1px 3px var(--color-sidebar-shadow);
}

.pipeline-sidebar {
  position: absolute;
  top: -1px; /* Avoids pixel rounding gaps */
  bottom: -1px;
  left: 80px;
  z-index: 1;
  display: flex;
  width: 100%;
  transform: translateX(calc(-100% + 56px));
  transition: transform ease 0.4s;
}
.pipeline-sidebar--no-global-toolbar {
  left: 0;
}
@media (min-width: 1200px) {
  .pipeline-sidebar {
    z-index: 3;
  }
}
@media (min-width: 700px) {
  .pipeline-sidebar {
    width: 400px;
    transform: translateX(-344px);
  }
}
.pipeline-sidebar--visible {
  transform: translateX(0);
}
@media print {
  .pipeline-sidebar {
    display: none;
  }
}
.pipeline-sidebar--visible {
  transform: translateX(0);
}

.pipeline-ui {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
  background: var(--color-bg-3);
  visibility: hidden;
  transition: visibility 0.3s;
}
.pipeline-sidebar--visible .pipeline-ui {
  visibility: visible;
  transition: visibility 0s;
}
.pipeline-ui--experiment-tracking {
  overflow-y: scroll;
}

.pipeline-toolbar {
  align-items: center;
  background: var(--color-bg-2);
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  width: 56px;
  z-index: 2;
}

.compare-switch-wrapper__text {
  font-size: 14px;
}
