.pipeline-primary-toolbar {
  display: block;
  list-style: none;
  margin: 0;
  padding: 1em 0;
}

.pipeline-primary-toolbar li {
  display: flex;
  justify-content: center;
}

.pipeline-menu-button svg {
  transition: transform ease 0.4s;
}

.pipeline-menu-button--inverse svg {
  transform: rotate(-180deg);
}
