/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.kedro-pipeline {
  position: relative;
  height: 100%;
  overflow: hidden;
  color: var(--color-text);
  background: var(--color-bg-1);
}
@media print {
  .kedro-pipeline.kui-theme--light {
    background: none;
  }
}

.pipeline-title {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}
