/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.details-mainframe {
  background-color: var(--color-sidebar-shadow);
  bottom: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-start;
  left: 80px;
  overflow-y: scroll;
  position: absolute;
  top: 0;
  transform: translateX(56px);
  transition: transform 0.4s ease, width ease 0.4s;
  width: calc(100% - 56px - 80px);
}
@media (min-width: 700px) {
  .details-mainframe--sidebar-visible {
    width: calc(100% - 400px - 80px);
    transform: translateX(400px);
  }
}
