/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
/**
 * List of colour variables for the different parts of the minimap
 */
.kui-theme--light {
  --minimap-bg: #abb2b7;
  --minimap-shadow: #e6e7e9;
  --minimap-viewport-stroke: #d9dcde;
  --minimap-node-fill-default: #f2f3f4;
  --minimap-node-fill-active: #ffffff;
  --minimap-node-fill-selected: #00e3ff;
  --minimap-node-fill-faded: #c6cace;
}

.kui-theme--dark {
  --minimap-bg: #051925;
  --minimap-shadow: #253742;
  --minimap-viewport-stroke: #989898;
  --minimap-node-fill-default: #2a3b46;
  --minimap-node-fill-active: #2a3b46;
  --minimap-node-fill-selected: #00bcff;
  --minimap-node-fill-faded: #091d29;
}

.pipeline-minimap-node rect {
  fill: var(--minimap-node-fill-default);
}

.pipeline-minimap-node--active rect {
  fill: var(--minimap-node-fill-active);
}

.pipeline-minimap-node--selected rect {
  fill: var(--minimap-node-fill-selected);
}

.pipeline-minimap-node--faded rect {
  fill: var(--minimap-node-fill-faded);
}

.pipeline-minimap__graph {
  display: block;
}

.pipeline-minimap-container {
  /* Sub-pixel adjustment */
  position: absolute;
  top: calc(100% + 2px);
  left: 100%;
  width: 500px;
  height: 222px;
  background: var(--minimap-bg);
  box-shadow: 0 0 2px var(--minimap-shadow);
  transform: translate(-100%, -100%);
  transition: transform 300ms ease;
}

.pipeline-minimap {
  position: absolute;
  top: 0;
  right: 0;
}

.pipeline-minimap__viewport {
  cursor: move;
  fill: transparent; /* Solid for pointer events */
  stroke-width: 1px;
  stroke: var(--minimap-viewport-stroke);
}
